.social-icons-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.social-icon {
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  margin: 21px;
}

.social-icon > img {
  width: 50px;
}

.social-icon:hover {
  filter: drop-shadow(0px 8px 8px rgba(0, 0, 0, 0.32));
  cursor: pointer;
  transition: all 0.3s;
}

@media only screen and (max-width: 640px) {
  .social-icon {
    margin: 8px 21px;
  }
}