body {
  min-height: 100vh;
  box-sizing: border-box;
  margin: 0;
  width: 100%;
  overflow-x: hidden;
  font-family: 'Poppins', sans-serif;
}

.section-container {
  box-sizing: border-box;
  min-height: 100vh;
  position: relative;
  padding: 50px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
}

@media only screen and (max-width: 640px) {
  .section-container {
    padding: 100px 0;
    
  }
}
.home-container {
  background: linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  min-height: 100vh;
  padding: 100px 10%;
  box-sizing: border-box;
}

.header-text {
  display: flex;
  flex-direction: column;
  text-align: center;
}

.header-text > h1 {
  font-family: Poppins;
  font-style: normal;
  font-weight: 900;
  font-size: 3em;
  line-height: 62px;
  text-align: center;
  letter-spacing: 0.2px;
  color: #fff;
  margin: 10px;
}

.header-text > p {
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  color: #e5e5e5;
  margin: 10px;
}

.head-btns {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin: 35px 10px;
}

.btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0px 36px;
  height: 52px;
  left: 0;
  top: 0px;
  text-decoration: none;
  box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
  margin: 4px;
  border-radius: 39px;
}

.btn-white {
  background-color: #fff;
  color: #112d57;
}

.btn-transparent {
  border: 1px solid #fff;
  box-sizing: border-box;
  color: #fff
}

.btn:hover {
  box-shadow: 1px 10px 32px rgba(0, 0, 0, 0.45);
  transition: all 0.3s ease-in-out;
}

.btn-text {
  font-family: Poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  letter-spacing: 0.2px;
  text-align: center;
}

.splash-image {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.home-anime {
  margin-bottom: -80px;
}

@media only screen and (max-height: 550px) {
  .home-anime {
    width: 400px;
  }
}

@media only screen and (max-width: 640px) {
  .home-anime {
    width: 80%;
    align-self: center;
    margin-bottom: -20px;
  }

  .head-btns {
    margin: 10px 10px 35px 10px;
  }
}


.about-main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 60px 80px 80px 80px;
}

.about-main-left,
.about-main-right {
  width: 50%;
}

.about-main-right {
  display: flex;
  justify-content: flex-end;
}

.about-main-left {
  display: flex;
  flex-direction: column;
  justify-content: center;

}

.about-sub-head {
  margin: 0;
}

.about-details {
  margin: 10px 0 32px 0;
}

.vector-frame {
  position: absolute;
  z-index: -10;
  left: 0;
  bottom: 0;
  width: 55%;
}

.about-vector {
  display: block;
  max-height: 100%;
  max-width: 100%;
}

.profile {
  width: 300px;
  max-width: 100%;
  max-height: 100%;
  border-radius: 32px;
}

@media only screen and (max-width: 640px) {
  .vector-frame {
    width: 90%
  }

  .about-head {
    width: 80%;
  }

  .about-head > p {
    font-size: 1.2em;
    line-height: 1.2em;
  }

  .about-main {
    padding: 50px 32px;
  }

  .about-main-left,
  .about-main-right {
    width: 100%;
  }
}
.header-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60%;
  align-self: center;
}

.header-container > h1 {
  font-style: normal;
  font-weight: 500;
  font-size: 2.9em;
  line-height: 57px;
  text-align: center;
  letter-spacing: 0.2px;
  color: #000;
  margin: 0;
}

.header-container > p {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  font-size: 1.4em;
  line-height: 1.4em;
  text-align: center;
  margin: 10px 0;
}

@media only screen and (max-width: 640px) {
  .header-container {
    width: 80%;
  }
  .header-container > p {
    font-size: 1.2em;
    line-height: 1.2em;
  }
}
.footer {
  font-family: Montserrat;
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  text-align: center;
  letter-spacing: -0.015em;
}

.footer-link {
  color: #000;
}
.project-cards-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 44px;
  align-self: center;
  justify-content: center;
}

.project-card {
  width: 500px;
  min-height: 550px;
  background-color: #fff;
  box-shadow: 10px 40px 50px rgba(229, 233, 246, 0.4);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.project-card:hover {
  box-shadow: 0px 40px 50px rgba(0, 0, 0, 0.4);
  transition: all 0.3s;
}

.image-container {
  width: 100%;
  border-radius: 20px;
}

.project-external-link {
  border-radius: 20px 20px 0 0;
}

.project-image {
  display: block;
  border-radius: inherit;
  max-height: 100%;
  max-width: 100%;
}

.project-details-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-self: flex-start;
  padding: 20px;
}

.project-heading {
  margin: 0;
}

.project-details {
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  color: #7d7987;
}

@media only screen and (max-width: 640px) {
  .project-cards-container {
    padding: 20px;
  }

  .project-card {
    width: 290px;
    min-height: 500px;
  }
}
.skill-card-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.skill {
  display: flex;
  flex-direction: column;
  width: 150px;
  align-items: center;
  margin-bottom: 20px;
}

.skill > img {
  border-radius: 500px;
  width: 108px;
  height: 108px;
  margin-bottom: 15px;
  -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.skill > p {
  font-size: 24px;
  font-family: Montserrat;
  line-height: 36px;
  text-align: center;
  letter-spacing: -0.015em;
  color: #000;
  margin: 0;
}

.skills-vector-frame {
  position: absolute;
  display: flex;
  flex-direction: row-reverse;
  right: 0;
  bottom: 0;
  z-index: -10;
  width: 80%;
}

.skills-vector {
  max-height: 100%;
  width: 100%;
}

@media only screen and (max-width: 640px) {
  .skills-vector-frame {
    width: 90%;
  }
  
  .skill-card-container {
    margin: 80px 10%;
  }
}

.social-icons-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.social-icon {
  -webkit-filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
          filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  margin: 21px;
}

.social-icon > img {
  width: 50px;
}

.social-icon:hover {
  -webkit-filter: drop-shadow(0px 8px 8px rgba(0, 0, 0, 0.32));
          filter: drop-shadow(0px 8px 8px rgba(0, 0, 0, 0.32));
  cursor: pointer;
  transition: all 0.3s;
}

@media only screen and (max-width: 640px) {
  .social-icon {
    margin: 8px 21px;
  }
}
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 36px;
  left: 32px;
  top: 32px;
}

.bm-burger-bars {
  background-color: #373a47;

}

.bm-burger-bars-hover {
  background-color: #000;
}

.bm-cross-button {
  height: 24px;
  width: 24px;
}

.bm-cross {
  background-color: #fff;
}

.bm-menu-wrap {
  position: fixed;
  height: 100vh;
  overflow-x: hidden;
  box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
}

.bm-menu {
  background: linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%);
  padding: 2.5em 1.5em 0 0;
  height: 100vh;
  overflow: hidden !important;
}

.bm-item-list {
  padding: 0.8em;
}

.menu-item {
  position: relative;
  font-size: 2em;
  text-decoration: none;
  color: #fff;
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  padding: 10px;
}

.menu-item::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 0;
  bottom: 0;
  left: 0;
  background-color: #fff;
  visibility: hidden;
  transition: all 0.5s ease-in-out;
}

.active-item {
  font-weight: 900;
}

.menu-item:hover::before, .active-item::before {
  visibility: visible;
  height: 3px;
}

.go-home-btn {
  position: fixed;
  bottom: 28px;
  right: 28px;
  width: 60px;
  border-style: none;
  border-radius: 30px;
  box-shadow: 0px 4px 31px rgba(0, 0, 0, 0.15);
}

.go-home-btn:hover {
  cursor: pointer;
  box-shadow: 1px 10px 32px rgba(0, 0, 0, 0.45);
  transition: all 0.3s ease-in-out;
}

.white-bkg {
  background-color: #fff;
}

.gradient-bkg {
  background: linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%);
}

.home-icon {
  width: 40px;
}
